/* Menu Trigger Hamburger */
.navbar-toggler {
  position: relative;
  width: 24px;
  height: 24px;
  z-index: 40;
  transition: all .2s;
  outline: none;
  border: 0;
  margin: 0 0 0 10px;
  vertical-align: bottom; }
  .navbar-toggler:focus {
    outline: none; }
  .navbar-toggler:after, .navbar-toggler:before {
    display: none;
    content: "";
    position: absolute;
    top: 0;
    left: 11px;
    width: 2px;
    height: 20px;
    background-color: #111111;
    border-radius: 5px;
    transform: rotate(45deg);
    transition: all .2s; }
  .navbar-toggler:before {
    transform: rotate(-45deg); }
  .navbar-toggler[aria-expanded="false"] span {
    position: absolute;
    top: 50%;
    left: 0;
    width: 24px;
    height: 2px;
    margin-top: -2px;
    background-color: #111111;
    border-radius: 5px; }
    .navbar-toggler[aria-expanded="false"] span:after, .navbar-toggler[aria-expanded="false"] span:before {
      content: "";
      position: absolute;
      top: -6px;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: #111111;
      border-radius: 5px;
      transition: all .2s; }
    .navbar-toggler[aria-expanded="false"] span:before {
      top: 6px; }
  .navbar-toggler[aria-expanded="true"] span {
    display: none; }
  .navbar-toggler[aria-expanded="true"]:after, .navbar-toggler[aria-expanded="true"]:before {
    display: block; }
  .navbar-toggler[aria-expanded="true"]:active {
    transform: rotate(90deg) scale(0.9); }
  .navbar-toggler:active span:after {
    top: -10px; }
  .navbar-toggler:active span:before {
